<template>
  <div>

    <div class="my-3">

      <h3 class="text-center text-white p-3 bg-info rounded shadow-sm d-flex flex-column justify-content-center">
        <span class="mr-1 h2" style="color: #00ffff">
          تعداد زیر مجموعه‌های شما:
        </span>
        <b class="font-weight-bolder">
          3,429
        </b>
      </h3>

      <hr/>
      <div class="my-3">
        <h2 class="text-center">دعوت از دوستان</h2>
        <p class="text-muted text-justify text-last-center">
          در این قسمت شما می‌توانید با استفاده از ایمیل و یا شماره همراه، دوستانتان را دعوت کنید و از عضویت و فعالیت
          آنها سود ببرید.
        </p>

        <div class="row mx-0 my-2">

          <div class="col-md-6">

            <div class="card border-primary">
              <h3 class="card-header bg-primary text-white text-center">
                <span class="fa fa-envelope mr-1"></span>
                <span>دعوت با استفاده از ایمیل</span>
              </h3>
              <div class="card-body">

                <div class="form-group">
                  <label for="email-input">ایمیل دوستتان را وارد کنید</label>
                  <div class="input-group">
                    <input id="email-input" type="email" class="form-control" v-model="email" placeholder="ایمیل"/>
                    <div class="input-group-append">
                      <input type="button" class="btn btn-success" @click="addEmail" value="اضافه کردن"/>
                    </div>
                  </div>
                </div>

                <div class="form-group">
                  <ul>
                    <li v-for="(x,i) in emails" :key="i" class="d-flex align-items-center">
                      <span>{{ x }}</span>
                      <span class="cursor-pointer text-danger ml-auto" @click="emails.splice(i,1)">
                        <span class="fa fa-times"></span>
                      </span>
                    </li>
                  </ul>
                </div>

                <div class="form-group">
                  <label for="invitation-content-input">محتوای دعوت نامه</label>
                  <textarea class="form-control" id="invitation-content-input">
                    سلام، به آدرس زیر بیا و ثبت نام کن و درآمد کسب کن
                    http://site.com/registration
                  </textarea>
                </div>

                <div class="form-group">
                  <button class="btn btn-success w-100">
                    ارسال دعوت نامه
                  </button>
                </div>

              </div>
            </div>

          </div>

          <div class="col-md-6">

            <div class="card border-info">
              <h3 class="card-header bg-info text-white text-center">
                <span class="fa fa-mobile-alt mr-1"></span>
                <span>دعوت با استفاده از شماره همراه</span>
              </h3>
              <div class="card-body">

                <div class="form-group">
                  <label for="mobile-input">شماره همراه دوستتان را وارد کنید</label>
                  <div class="input-group">
                    <input id="mobile-input" type="tel" class="form-control" v-model="mobile"
                           placeholder="شماره همراه"/>
                    <div class="input-group-append">
                      <input type="button" class="btn btn-success" @click="addMobile" value="اضافه کردن"/>
                    </div>
                  </div>
                </div>

                <div class="form-group">
                  <ul>
                    <li v-for="(x,i) in mobiles" :key="i" class="d-flex align-items-center">
                      <span>{{ x }}</span>
                      <span class="cursor-pointer text-danger ml-auto" @click="mobiles.splice(i,1)">
                        <span class="fa fa-times"></span>
                      </span>
                    </li>
                  </ul>
                </div>

                <div class="form-group">
                  <label for="invitation-mobile-content-input">محتوای دعوت نامه</label>
                  <textarea class="form-control" id="invitation-mobile-content-input">
                    سلام، به آدرس زیر بیا و ثبت نام کن و درآمد کسب کن
                    http://site.com/registration
                  </textarea>
                </div>

                <div class="form-group">
                  <button class="btn btn-success w-100">
                    ارسال دعوت نامه
                  </button>
                </div>

              </div>
            </div>

          </div>

        </div>
      </div>

      <hr/>
      <div class="mt-4 table-responsive">
        <h2 class="text-center">آخرین افراد زیر مجموعه</h2>
        <table class="table">
          <thead>
          <tr class="text-center bg-info text-white">
            <th>نام کاربری</th>
            <th>تاریخ پیوستن</th>
          </tr>
          </thead>

          <tbody>
          <tr v-for="x in 10" :key="x">
            <td>User {{ x }}</td>
            <td class="text-center">1399/1/{{ x + 3 }}</td>
          </tr>
          </tbody>
        </table>
      </div>


    </div>

  </div>
</template>

<script>
export default {
  name: "Subsets",
  data: () => ({
    emails: [],
    email: '',
    mobiles: [],
    mobile: ''
  }),
  methods: {
    addEmail: function () {
      if (this.email && this.email.trim() !== '') {
        this.emails.push(this.email)
        this.email = '';
      }
    },
    addMobile: function () {
      if (this.mobile && this.mobile.trim() !== '') {
        this.mobiles.push(this.mobile)
        this.mobile = '';
      }
    }
  }
}
</script>

<style scoped>

</style>